@import url(https://fonts.googleapis.com/css?family=Mountains+of+Christmas);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(top, rgb(45, 115, 198), rgb(80, 224, 245));
  background: linear-gradient(to bottom, rgb(45, 115, 198), rgb(80, 224, 245));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    font-family: 'Mountains of Christmas', cursive;
     color: #0ccac4;
}
